
section#Rev {

  .container {

    .card {
      display: none;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: inherit;
      height: inherit;
      background-color: black;
      border-radius: 10px;

      .review {
        width: inherit;
        height: 100%;
        //max-height: 105px;
        border: 2px solid black;
        border-radius: 10px;
        background-color: white;
        color: black;
        padding: 11px 30px;

        p {
          font-family: 'Roboto';
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 14px;
        }
      }

    }
  }
}

@media screen and (min-width: 576px) {
  section#Rev {
    .container {

      .card {
        display: none;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        width: auto;
      }

    }
  }
}

@media screen and (min-width: 768px) {
  section#Rev {

    .container {

      .card {
        display: none;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        max-width: 373px;
        height: inherit;
        background-color: black;
        border-radius: 15px;

        .review {
          width: inherit;
          height: 100%;
          border: 2px solid black;
          border-radius: 15px;
          background-color: white;
          color: black;
          padding: 33px 33px;

          p {
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
          }
        }

      }

    }

  }
}

@media screen and (min-width: 1200px) {
  section#Rev {
    .container {
      .card {

      }

      .mw1200 {
        display: flex !important;
      }
    }
  }
}
