
@import url(https://fonts.googleapis.com/css2?family=Marcellus+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=El+Messiri&display=swap);
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
section#Rev{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 250px;
  margin-top: 40px;
  h1{
    font-family: 'El Messiri';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: black;
  }
  .container{
    align-items: center;
    width: inherit;
    height: 135px;

    .mw1{
      display: flex!important;
    }
  }

  p{
    font-family: 'El Messiri';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: black;
  }
}

@media screen and (min-width: 576px) {
  section#Rev{
    .container{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      gap: 20px;
      align-items: center;
      width: inherit;
      height: 135px;
      .mw576{
        display: flex!important;
      }
    }
  }
}

@media screen and (min-width: 768px){
  section#Rev{
    height: 600px;
    margin-top: 100px;
    h1{
      font-weight: 400;
      font-size: 48px;
      line-height: 75px;
      margin-bottom: 10px;
    }
    p{
      font-weight: 400;
      font-size: 48px;
      line-height: 75px;
    }
    .container{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      width: inherit;
      height: 275px;
      gap: 7%;

      .mw768{
        display: flex!important;
      }
    }
    .points{
      width: 86px;
      height: 20px;
      .point{
        width: 20px;
        height: 20px;
        border-radius: 50px;
        border: 2px solid black;
      }
    }
  }
}

@media screen and (min-width: 1200px) {
  section#Rev{
    .container{

      .mw1200{
        display: flex!important;
      }
    }
  }
}
