@import url(https://fonts.googleapis.com/css2?family=Marcellus+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=El+Messiri&display=swap);
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

section#MainBanner{
  width: 100%;
  height: 150px;
  border-radius: 10px;
  background: url(../../../../../images/main-banner.jpg) no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  color: white;
  padding: 62px 0 16px 0;
  .container{
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    h1{
      font-family: "Marcellus SC";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
    }
    p{
      font-family: "El Messiri";
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 19px;
    }
    button{
      width: 104px;
      height: 27px;
      background: #E11514;
      border-radius: 50px;
      font-family: "Roboto";
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: white;
    }
  }
}

@media screen and (min-width: 768px){
  section#MainBanner{
    height: 300px;
    padding: 100px 0 40px 0;
    border-radius: 15px;
    .container{
      h1{
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 40px;
      }
      p{
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
      }
      button{
        width: 170px;
        height: 50px;
        font-weight: 400;
        font-size: 18px;
        line-height: 15px;
      }
    }
  }
}

@media screen and (min-width: 992px){
  section#MainBanner{
    height: 516px;
    padding: 190px 0 100px 0;
    .container{
      h1{
        font-style: normal;
        font-weight: 400;
        font-size: 64px;
        line-height: 77px;
      }
      p{
        font-weight: 400;
        font-size: 36px;
        line-height: 56px;
      }
      button{
        width: 218px;
        height: 68px;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
      }
    }
  }
}