@import url(https://fonts.googleapis.com/css2?family=Marcellus+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=El+Messiri&display=swap);
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
section#MainPopularCategories {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 190px;
  margin-top: 15px;
  color: black;
  font-family: 'El Messiri';

  .cards {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 137px;
    gap: 10px;

    div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 28px;
    }

    .card {
      display: flex;
      width: 100%;
      max-width: 107px;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      height: inherit;
      background-color: black;
      border-radius: 10px;
      color: white;
      overflow: hidden;

      img {
        border-radius: 10px;
        width: inherit;
        height: 109px;
        object-fit: cover;
      }
    }

    .mw450 {
      display: none;
    }

    .mw576 {
      display: none;
    }

    .mw768 {
      display: none;
    }
  }
}


@media screen and (min-width: 450px) {
  .cards {
    .mw450 {
      display: flex !important;
    }
  }
}

@media screen and (min-width: 576px) {
  .cards {
    .mw576 {
      display: flex !important;
    }
  }
}



@media screen and (min-width: 768px) {
  section#MainPopularCategories {
    height: 490px;
    font-family: 'El Messiri';
    font-weight: 400;
    font-size: 48px;
    line-height: 75px;
    margin-top: 0;

    .cards {
      height: 341px;
      gap: 20px;

      .card {
        max-width: 236px;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
        text-align: center;
        border-radius: 15px;

        img {
          border-radius: 15px;
          width: inherit;
          height: 270px;
          object-fit: cover
        }
      }

      div {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 71px;
      }

      .mw576 {
        display: block;
      }
      .mw768 {
        display: block;
      }
    }

    .points {
      display: none;
    }
  }
}
